import * as report from "./report";
const displayHeadline = function (headline) {
    const headlineElement = (document.querySelector(".report__headline"));
    headlineElement.textContent = headline;
};
const displayIcon = function (weatherData) {
    const iconElement = document.querySelector(".temperature__icon");
    iconElement?.classList.replace(
    // replaces previous icon with new icon
    iconElement.classList[3], report.getIconClass(weatherData));
    setIconAnimation();
};
const displayTemperature = function (weatherData) {
    const tempElement = (document.querySelector(".temperature__value"));
    tempElement.textContent = `${Math.round(weatherData.temp)}°F`;
};
const displaySuggestions = function (suggestions) {
    const suggestionsElementContainer = document.querySelector(".report__suggestions");
    suggestions.forEach((suggestion) => {
        const suggestionElement = document.createElement("li");
        suggestionElement.classList.add("report__suggestions--active");
        suggestionElement.textContent = suggestion;
        suggestionsElementContainer?.appendChild(suggestionElement);
    });
};
const displayHiAndLo = function (weatherData) {
    const tempRangeElement = (document.querySelector(".temperature__range"));
    tempRangeElement.textContent = `H: ${Math.round(weatherData.tempMax)}°F L: ${Math.round(weatherData.tempMin)}°F`;
};
const displayCityName = function (weatherData) {
    const cityNameElement = (document.querySelector(".detailed-view__header"));
    cityNameElement.textContent = weatherData.name;
};
const clearSuggestionsContainer = function () {
    const suggestionsElementContainer = document.querySelector(".report__suggestions");
    while (suggestionsElementContainer?.firstChild) {
        suggestionsElementContainer.removeChild(suggestionsElementContainer.firstChild);
    }
};
const displayErrorMsg = function () {
    const errorMsgElement = (document.querySelector(".report__headline"));
    errorMsgElement.textContent =
        "Sorry, we could not find the city you inputted :/";
    displaySuggestions(["You should input a proper city name"]);
};
const renderDetailedView = function (weatherData) {
    const fieldsTextContent = report.getDetailedFields(weatherData);
    const detailedInformationFields = (document.querySelectorAll(".detailed-view__fields"));
    for (let i = 0; i < detailedInformationFields.length; i++) {
        detailedInformationFields[i].textContent = fieldsTextContent[i];
    }
};
const setIconAnimation = function () {
    const iconElement = document.querySelector(".temperature__icon");
    const iconClassList = iconElement.classList;
    const iconClassName = iconElement.classList[iconClassList.length - 1];
    console.log(iconClassList[iconClassList.length - 1]);
    if (iconClassList[3] === "fa-sun") {
        iconClassList.replace(iconClassName, "fa-spin");
    }
    else {
        iconClassList.replace(iconClassName, "fa-beat-fade");
    }
};
const resetDisplay = function () {
    const headlineElement = (document.querySelector(".report__headline"));
    const tempElement = (document.querySelector(".temperature__value"));
    const tempRangeElement = (document.querySelector(".temperature__range"));
    headlineElement.textContent = "";
    tempElement.textContent = "";
    tempRangeElement.textContent = "";
    clearSuggestionsContainer();
};
export { displayCityName, displayIcon, displayHeadline, displayHiAndLo, displaySuggestions, displayTemperature, displayErrorMsg, renderDetailedView, setIconAnimation, resetDisplay, };
